import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';
import domtoimage from 'dom-to-image';

import { TaskforceService } from '../taskforce.service';

@Component({
  selector: 'relatorio',
  templateUrl: './relatorio.component.html',
  styleUrls: ['./relatorio.component.css'],
})
export class RelatorioComponent implements OnInit {
  constructor(private taskForceService: TaskforceService) {}

  @Input() keySala: string;
  @Input() vidas: number;
  @Input() numRodada: number;
  @Input() keyProfissaoSorteada: string;
  @Input() listaProfissoesSorteadas: Observable<any>;
  @Input() flagRelatorio: boolean;
  @Output() flagRelatorioChange = new EventEmitter();
  @Input() relatorioPartida: any;
  @Output() relatorioPartidaChange = new EventEmitter();

  textoCompartilhar: string = "Compartilhar";
  lista: any = [];
  vida: number;
  desabilitado: boolean;

  baseUrl = this.taskForceService.baseUrl() + 'assets/images/';

  async ngOnInit() {
    let i: number = 0;
    let j: number = 0;
    this.vida = this.vidas;
    this.desabilitado = true;

    this.listaProfissoesSorteadas.pipe(take(1)).subscribe(async (l) => {
      for (i = 0; i < l.length; i++) {
        this.lista.push({
          imagem: l[i].imagem,
          responsavel: l[i].responsavel,
          profissao: l[i].profissao,
          acertos: l[i].acertos,
          erros: l[i].erros,
        });

        for (j = 0; j < this.relatorioPartida.length; j++) {
          if (this.relatorioPartida[j].responsavel === l[i].responsavel) {
            this.relatorioPartida[j].imagem = l[i].imagem;
            this.relatorioPartida[j].responsavel = l[i].responsavel;
            this.relatorioPartida[j].profissao = l[i].profissao;
            this.relatorioPartida[j].acertos += l[i].acertos;
            this.relatorioPartida[j].erros += l[i].erros;
            break;
          }
        }

        if (j === this.relatorioPartida.length) {
          this.relatorioPartida.push({
            imagem: l[i].imagem,
            responsavel: l[i].responsavel,
            profissao: l[i].profissao,
            acertos: l[i].acertos,
            erros: l[i].erros,
          });
        }

        this.relatorioPartidaChange.emit(this.relatorioPartida);

        await this.resetarProfissao();
      }
    });
    this.desabilitado = false;
  }

  async fecharRelatorio() {
    if (!this.desabilitado) {
      this.desabilitado = true;
      if (this.vida <= 0) {
        this.relatorioPartida = [];
        this.relatorioPartidaChange.emit(this.relatorioPartida);
        await this.taskForceService.resetarSala(this.keySala);
      }
      this.flagRelatorio = false;
      this.flagRelatorioChange.emit(this.flagRelatorio);
    }
  }

  async resetarProfissao() {
    await this.taskForceService.resetarProfissao(
      this.keySala,
      this.keyProfissaoSorteada
    );
  }

  mobileCheck():boolean {
    let check = false;
    (function(a){if(/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(a)||/1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0,4))) check = true;})(navigator.userAgent||navigator.vendor||(window as any).opera);
    return check;
  };

  async compartilhar() {
    this.textoCompartilhar = 'Gerando imagem...';
    await domtoimage.toBlob(document.querySelector(".tabela-relatorio"))
    .then(async (blob) => {
        //@ts-ignore
        const imgClip = new ClipboardItem({ "image/png" : blob });
        await (navigator.clipboard as any).write([imgClip]);

        if(this.mobileCheck()) {
          const data = {
            files: [
              new File([blob], 'file.png', {
                type: blob.type,
              }),
            ],
            title: "",
            text: "Rodada " + this.numRodada + " alcançada!\nhttps://squadtask.io",
          };
          try {
            if (!((navigator as any).canShare(data))) {
              throw new Error("Can't share data.");
            }
            await navigator.share(data);
          } catch (err) {
            console.error(err.name, err.message);
          }
        }
        this.textoCompartilhar = 'Copiado p/ ctrl-v';
    })
    .catch((error) => {
        this.textoCompartilhar = 'Falha ao copiar';
        console.error('oops, something went wrong!', error);
    });

    setTimeout(() => {
      this.textoCompartilhar = 'Compartilhar';
    }, 3000);
  }
}
